export default {
	components: {
		appTabs,
		loadMore
	},
	data() {
		return {
			nextPage:"",
			loadingType:0,
			tabs: ["待使用", "已使用","已过期"],
			tabIndex: 0, //默认常用地址
			
			type:0,
			discountList:[,,,,],
			countType:1, //1小轿车 2suv 3mvp
		}
	},
	onLoad(e) {
		
	    if(e.type ==1){
			//优惠券
			this.type =1;
