				title:"我的洗车券"
			})
		}else{
			//折扣券
			this.type =2;
			uni.setNavigationBarTitle({
				title:"优惠券"
			})
		}
	},
	
	//触底加载更多数据
	onReachBottom(){
		
	},
	methods: {
		changeIndex(e){
			console.log(e)
			this.tabIndex = e;
		}
